@import './variables.scss';

.home{
    margin: auto;
    text-align: center;
    width: 80%;
    max-width: 1200px;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('https://res.cloudinary.com/djvwk7zf2/image/upload/v1676284938/logo_gdxcay.jpg');
}
.mainDiv a{
    font-size: xx-large;
    color: $Background !important;
    background-color: $Fontcolor !important;

}
.mainDiv button{
    border-radius: 5%;
    color: $Background !important;
    background-color: $Fontcolor !important;
}

.mainDiv button:hover{
    color: $Fontcolor !important;
    background-color: $Background !important;
}
.mainDiv a:hover{
    opacity: 90%;
    font-size: xx-large !important;
    color: $Fontcolor !important;
    background-color: $Background !important;
}
@media (max-width: 700px) {
    .home{
        margin: auto;
        text-align: center;
        width: 100%;
        height: 100vh;
        background-repeat:no-repeat;
        background-position:center;
        background-image: url('https://res.cloudinary.com/djvwk7zf2/image/upload/v1676316225/logo_phone_l9fft4_wz1wpf.webp');
    }
}